@mixin arrow {
  background-color: #000;
  z-index: 1;
  position: absolute;
  border-radius: $arrowRadius;
  width: $arrowWidth;
}

@mixin circle($clockSize) {
  width: $clockSize;
  height: $clockSize;
}

@mixin smallCircle($smallRoundSize) {
  width: $smallRoundSize;
  height: $smallRoundSize;
}
